import Peer from 'simple-peer'

export const peer = {
  data () {
    return {
      roomTitle: null,
      signalText: null,
      roomSignal: null,
      joinRoom: false
    }
  },
  methods: {
    createPeer () {
      const peer = new Peer({ initiator: true })
      peer.on('signal', data => {
        this.signalText = JSON.stringify(data)
      })
      peer.on('connect', () => {
        peer.send(`${this.roomTitle} connected`)
      })
      peer.on('data', data => {
        // got a data channel message
        console.log(`got a message from:` + data)
      })
    },
    joinPeer () {
      const peer = new Peer()
      peer.signal(JSON.parse(this.roomSignal))
      peer.on('connect', () => {
        peer.send(`connected`)
      })
      peer.on('data', data => {
        // got a data channel message
        console.log(`got a message from:` + data)
      })
    }
  }
}
